.breakpoint__medium-up .home-left{display:flex;flex-direction:column;justify-content:center;padding-left:50px}.breakpoint__medium-up .home-intro{font-size:60px}.breakpoint__medium-up .home-button{font-family:'Raleway';font-weight:bold;font-size:30px;text-transform:lowercase;background-color:#1E1E1E;border:1px solid #f1b10e;color:#f1b10e}.breakpoint__medium-up .home-button:hover{color:#f1b10e;background-color:#f1b10e;color:#1E1E1E}.breakpoint__medium-up .home-button-divider{padding-top:20px}.breakpoint__small-down .home-intro{font-size:45px}.breakpoint__small-down .home-button{font-family:'Raleway';font-weight:bold;font-size:30px;text-transform:lowercase;background-color:lightgray;border:1px solid #f1b10e;color:#f1b10e}.breakpoint__small-down .home-button:hover{color:#f1b10e;background-color:#f1b10e;color:lightgray}.home{display:flex;height:100vh;color:#f1b10e;font-family:'Poppins', sans-serif}

.header {
  display: flex;
  align-items: center;
  height: 60px;
  font-family: 'Poppins', sans-serif;
  position: -webkit-sticky;
  position: sticky;
}

.header-left {
  flex: 1 1;
}

.header-right {
  display: flex;
  justify-content: space-evenly;
}

.header-nav {
  text-decoration: none;
}

.header-options {
  padding: 0 20px;
  color: black;
  font-size: 20px;
}

.header-options:hover {
  transition: 0.5s;
  color: rgb(241, 177, 14);
}
