.breakpoint__large-up .project-container{padding:20px}.breakpoint__large-up .project-image{width:650px;height:375px;object-fit:fill;border:1px solid black}.breakpoint__medium-down .project-container{padding:20px}.breakpoint__medium-down .project-image{height:225px;width:400px;object-fit:fill;border:1px solid black}.project-container:hover{filter:grayscale(100%);-webkit-filter:grayscale(100%);-webkit-transition:all 1s ease;cursor:pointer}.project-container{filter:grayscale(0%);filter:gray;-webkit-filter:grayscale(0%);filter:none;transition:1s ease}

