@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700&family=Poppins:wght@200;400&family=Raleway:wght@100;600&family=Source+Sans+Pro:wght@200&family=Work+Sans:wght@400;500&display=swap');

.breakpoint__large-up {
  .about {
    display: flex;
    padding: 50px;
  }

  .about-left {
    flex: 0.5;
  }

  .about-title {
    padding-bottom: 20px;
  }

  .about-right {
    flex: 0.5;
    color: white;
    font-size: 18px;
  }

  .about-details {
    padding-top: 65px;
  }
}

.breakpoint__medium-down {
  .about {
    display: flex;
    flex-direction: column;
    padding: 50px;
  }

  .about-image, .about-title {
    align-self: center;
    padding: 20px 0;
  }

  .about-details {
    padding: 20px 0;
  }
}

.about {
  font-family: 'Raleway';
}

.about-details {
  color: white;
  font-size: 18px;
}

.about-image {
  width: 500px;
  height: 400px;
}

.about-title {
  color: rgb(241, 177, 14);
  font-size: 40px;
  font-weight: bold;
}