@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700&family=Poppins:wght@200;400&family=Raleway:wght@100;600&family=Source+Sans+Pro:wght@200&family=Work+Sans:wght@400;500&display=swap');


.breakpoint__large-up {
  .contact-left {
    flex: 0.5;
    padding: 50px;
    display: flex;
    flex-direction: column;
  }
  
  .contact-right {
    flex: 0.6;
  }
}

.breakpoint__medium-down {
  .contact {
    flex-direction: column;
    padding: 40px 60px;
  }

  .contact-info {
    padding: 15px 0;
  }

  .contact-form {
    display: flex;
    flex-direction: column;
    padding-top: 10px;
    padding-bottom: 30px;
  }
  
  .contact-form-box {
    display: flex;
  }

  #name, #email:focus {
    outline: none;
  }

  #name, #email {
    background-color: lightgray;
    padding: 15px;
    font-family: 'Raleway';
    font-size: 15px;
    border: 1px solid grey;
    flex: 0.5;
  }

  #name {
    margin-right: 20px;
  }

  #map {
    align-self: center;
  }
}

.contact {
  display: flex;
  font-family: 'Raleway';
}

.contact-title {
  color: rgb(241, 177, 14);
  font-size: 40px;
  padding: 20px 0;
}

.contact-info {
  color: white;
  font-size: 18px;
  padding: 10px 0;
}

.contact-form {
  display: flex;
  flex-direction: column; 
  padding: 10px 0;
}


.contact-form > input, textarea, button {
  background-color: lightgray;
  padding: 15px;
  font-family: 'Raleway';
  font-size: 15px;
  border: 1px solid grey;
  margin-top: 20px;
}

.contact-form > input, textarea, button:focus {
  outline: none;
}

.contact-row {
  display: flex;
  align-items: center;
}

button {
  padding: 10px;
  border: 1px solid gray;
  background-color: lightgray;
  font-family: 'Raleway';
}

button:hover {
  background-color: rgb(241, 177, 14);
  transition: 0.5s;
  opacity: 0.8;
}

.contact-map-box {
  display: flex;
  flex-direction: column;
  font-size: 17px;
}

.contact-soc {
  text-decoration: none;
  padding-left: 10px;
}

