@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@700&family=Poppins:wght@200;400&family=Raleway:wght@100;600&family=Source+Sans+Pro:wght@200&family=Work+Sans:wght@400;500&display=swap');

.breakpoint__medium-up {
  .home-left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 50px;
  }
  
  .home-intro {
    font-size: 60px;
  }
  
  .home-button {
    font-family: 'Raleway';
    font-weight: bold;
    font-size: 30px;
    text-transform: lowercase;
    background-color: #1E1E1E;
    border: 1px solid rgb(241, 177, 14);
    color: rgb(241, 177, 14);
  }

  .home-button:hover {
    color: rgb(241, 177, 14);
    background-color: rgb(241, 177, 14);
    color: #1E1E1E;
  }

  .home-button-divider {
    padding-top: 20px;
  }
}

.breakpoint__small-down {
  .home-intro {
    font-size: 45px;
  }

  .home-button {
    font-family: 'Raleway';
    font-weight: bold;
    font-size: 30px;
    text-transform: lowercase;
    background-color: lightgray;
    border: 1px solid rgb(241, 177, 14);
    color: rgb(241, 177, 14);
  }

  .home-button:hover {
    color: rgb(241, 177, 14);
    background-color: rgb(241, 177, 14);
    color: lightgray;
  }
}

.home {
  display: flex;
  height: 100vh;
  color: rgb(241, 177, 14);
  font-family: 'Poppins', sans-serif;
  
}
